body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.header {
  border-bottom: 1px solid black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI"; }
  .header ul {
    margin: 0;
    padding: 5px;
    list-style: none;
    display: flex;
    justify-content: space-around;
    position: fixed;
    width: 100vw;
    background-color: #cbcbff;
    border-bottom: 1px solid black; }
  .header .activeNavLink {
    color: darkblue;
    text-decoration: none; }
  .header a {
    color: black;
    text-decoration: none; }
  .header a:active {
    padding: 2px;
    border-radius: 2px 2px 2px 2px;
    background-color: lightblue;
    text-decoration: none; }

.signupPage {
  height: 90vh;
  background: url(/bitcoin-app-react/static/media/Bitcoin.25b232f2.png) no-repeat fixed;
  background-position-x: center;
  background-position-y: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
  padding-top: 15vh; }
  .signupPage form {
    text-align: center; }
    .signupPage form input {
      font-size: 20px;
      border-radius: 10px 10px 10px 10px;
      margin: 5px;
      padding: 5px; }
    .signupPage form input:focus {
      outline: none; }
    .signupPage form .button {
      padding: 8px;
      margin: 15px;
      border-radius: 5px 5px 5px 5px;
      font-size: 20px;
      background-color: lightblue;
      font-family: Exo, sans-serif; }
    .signupPage form button:focus {
      outline: none; }

.movesList {
  border-radius: 10px 10px 10px 10px;
  margin: 3vw; }
  .movesList h1 {
    padding: 10px 0 0 10px;
    margin: 0; }
  .movesList ul {
    margin: 0;
    padding: 20px;
    list-style: none; }
    .movesList ul li {
      color: black;
      background-color: rgba(173, 216, 230, 0.753);
      width: 50vw;
      padding-right: 5px;
      border-radius: 10px 10px 10px 10px;
      margin: 5px; }
      .movesList ul li div {
        padding: 2px; }

.homePage {
  background: url(/bitcoin-app-react/static/media/bitcoin-bg.ede264f1.jpg) no-repeat center center;
  min-height: 91vh;
  padding-top: 43px;
  padding-left: 8px;
  color: white;
  font-family: Exo, sans-serif;
  font-size: 20px; }
  .homePage h4 {
    margin-left: 2vw; }

.contact {
  list-style: none;
  padding: 5px; }
  .contact a {
    text-decoration: none; }
  .contact .contactPreview {
    display: flex;
    align-items: center;
    background: #d4edf5;
    border-radius: 50px 50px 50px 50px;
    -webkit-transition: 0.3s;
    transition: 0.3s; }
    .contact .contactPreview img {
      border-radius: 50%;
      max-width: 80px; }
    .contact .contactPreview h2 {
      color: black;
      text-decoration: none;
      padding-left: 5px; }
  .contact .contactPreview:hover {
    background: #97dbf1; }

.contactList {
  list-style: none;
  padding: 0; }

.contactFilter {
  padding: 5px 0 0 10px; }
  .contactFilter input {
    font-size: 20px;
    border-radius: 5px 5px 5px 5px; }

.contactPage {
  padding-top: 8vh; }
  .contactPage .addContactButton {
    text-decoration: none;
    position: fixed;
    bottom: 5vh;
    right: 5vw;
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
    cursor: pointer;
    color: black; }
  .contactPage .addContactButton:hover {
    color: blue; }

.transferFund {
  background-color: lightgoldenrodyellow;
  border-radius: 10px 10px 10px 10px;
  margin: 3vw;
  padding: 5px; }
  .transferFund .inputContainer {
    padding-left: 20px; }
  .transferFund input {
    width: 100px;
    font-size: 16px; }
  .transferFund .button {
    padding: 2px;
    font-size: 16px;
    border-radius: 5px 5px 5px 5px;
    font-family: Exo, sans-serif;
    background-color: lightblue; }
  .transferFund button:focus {
    outline: none; }

.contactDetailsPage {
  padding-top: 8vh;
  font-family: Exo,sans-serif;
  list-style: none; }
  .contactDetailsPage h3 {
    padding-left: 10px; }
  .contactDetailsPage img {
    padding-left: 10px;
    border-radius: 50%; }
  .contactDetailsPage li {
    padding: 5px 5px 5px 20px; }
  .contactDetailsPage .buttons-container {
    display: flex;
    justify-content: space-between;
    padding: 20px 20px; }
    .contactDetailsPage .buttons-container .button {
      text-decoration: none;
      color: black;
      -webkit-transition: 0.3s;
      transition: 0.3s; }
    .contactDetailsPage .buttons-container .button:hover {
      cursor: pointer;
      color: #000096; }
  .contactDetailsPage a {
    color: black; }

.buttons {
  display: flex;
  justify-content: space-between;
  padding: 20px 20px; }

.contactEditPage {
  font-family: Exo,sans-serif;
  padding-top: 8vh; }
  .contactEditPage h3 {
    padding-left: 5px; }
  .contactEditPage h1 {
    text-align: center; }
    .contactEditPage h1 img {
      border-radius: 50%; }
  .contactEditPage li {
    padding-left: 5px; }
  .contactEditPage .buttons-container {
    display: flex;
    justify-content: space-between;
    padding: 20px 20px; }
    .contactEditPage .buttons-container .button {
      text-decoration: none;
      color: black;
      -webkit-transition: 0.3s;
      transition: 0.3s; }
    .contactEditPage .buttons-container .button:hover {
      cursor: pointer;
      color: #000096; }
  .contactEditPage .formContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .contactEditPage .formContainer input {
      border-radius: 5px 5px 5px 5px; }
    .contactEditPage .formContainer .save-button {
      font-size: 20px;
      font-weight: bold;
